@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&family=Poppins:wght@400;500;600&family=Roboto+Condensed:ital,wght@1,300&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* GOOGLE FONTS */
/* VARIABLES */
:root{
  --header-height: 3rem;
  --hue: 0 ;
  --sat: 0% ;
  --title-color:hsl(var(--hue),var(--sat),20%);
  --title-color-dark:hsl(var(--hue),var(--sat),0%);
  --text-color:hsl(var(--hue),var(--sat),46%);
  --body-color:hsl(var(--hue),var(--sat),98%);
  --container-color:#fff;
  /* font */
  --body-font:"Poppins",sans-serif;
  
  --big-font-size:3.5rem;
  --h1-font-size:2.25rem;
  --h2-font-size:1.5rem;
  --h3-font-size:1.25rem;
  --normal-font-size:1rem;
  --small-font-size:0.875rem;
  --smaller-font-size:0.813rem;
  --tiny-font-size:0.625rem;
  /* font weight */
  --font-normal:400;
  --font-medium:500;
  --font-semi-bold:600;
  /* MARGiN */
  --mb-0-25:0.25rem;
  --mb-0-5:0.5rem;
  --mb-0-75:0.75rem;
  --mb-1:1rem;
  --mb-1-5:1.5rem;
  --mb-2:2rem;
  --mb-2-5:2.5rem;
  --mb-3:3rem;
}
@media screen and (max-width:992) {
  :root{
    --big-font-size:2.75rem;
    --h1-font-size:1.5rem;
    --h2-font-size:1.25rem;
    --h3-font-size:1rem;
    --normal-font-size:0.938rem;
    --small-font-size:0.813rem;
    --smaller-font-size:0.75rem;
  }
} 
/* MAIN */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body,
button,
input,
textarea{
  font-family: "Poppins",sans-serif;
  font-family: var(--body-font);
  font-size: 1rem;
  font-size: var(--normal-font-size);
}
body{
  background-color: --body-color;
  color: hsl(0,0%,46%);
  color: var(--text-color);
}
h1,h2,h3{
  color: hsl(0,0%,20%);
  color: var(--title-color);
  font-weight:600;
  font-weight:var(--font-semi-bold);
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  cursor: pointer;
  border: none;
  outline: none;
}
img{
  max-width: 100%;
  height: auto;
}
/* REUSABLE CLASS */
  .section{
    padding: 6rem 0 2rem;
  }
  .sectionTitle{
    font-size: 2.25rem;
    font-size: var(--h1-font-size);
    color: hsl(0,0%,20%);
    color: var(--title-color);
  }
  .sectionSubtitle{
    display: block;
    font-size: 0.875rem;
    font-size: var(--small-font-size);
    margin-bottom: 4rem;
  }
  .sectionTitle,.sectionSubtitle{
    text-align: center;
  }
  /* LAYOUT */
  .container{
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
  }
  .grid{
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  /* BUTTONS */
  .btn{
    display: inline-block;
    background-color: hsl(0,0%,20%);
    background-color: var(--title-color) ;
    color: #fff;
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: 500;
    font-weight: var(--font-medium);
  }
  .btn:hover{
    background: hsl(0,0%,0%);
    background: var(--title-color-dark);
  }
  .btnIcon{
    margin-left: var(--mb-5);
  }
  .btnFlex{
    display: inline-flex;
    align-items: center;
  }

  
  /* Media */


  @media screen and (max-width: 992px) {
  .container {
      margin-left: 1.5rem;
      margin-left: var(--mb-1-5);
      margin-right: 1.5rem;
      margin-right: var(--mb-1-5);
    }

    .btn {
      padding: 1rem 1.75rem;
    }

    .btnIcon {
      width: 22px;
      height: 22px;
    }
  }
@media screen and (max-width:768px) {
  body{
    margin: 0 0 3rem 0;
    margin: 0 0 var(--header-height) 0;
  }
  .section {
    padding: 2rem 0 4rem;
  }
  .sectionSubtitle{
    margin-bottom: 3rem;
    margin-bottom: var(--mb-3);
  }
}

@media screen and (max-width:576px) {}

@media screen and (max-width:350px) {
  :root{
    --big-font-size: 2.25rem; 
  }
  .container {
      margin-left: 1rem;
      margin-left: var(--mb-1);
      margin-right: 1rem;
      margin-right: var(--mb-1);
    }
}








.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: var(--body-color);
}
.nav{
    height:calc( var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;

}
.navLogo,
.navToggle{
    color: var(--title-color);
    font-weight: var(--font-medium);
}
.navList{
    display: flex;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
}
.navLogo{
    font-size: var(--h2-font-size);
}
.navLink{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size:var(--h3-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;
}
.navIcon,
.navClose,
.navToggle{
    display: none;
}
/* change header */
.showHeader{
    box-shadow: 0 -1px 4px rgba( 0, 0, 0, 0.15);
}
/* ACTIVE_LINK */
.activeLink,
.navLink:hover{
    color: var(--title-color-dark);
}
/* BREACKPOINTS */
@media screen and ( max-width : 768px) {
    .header{
        top: initial;
        bottom: 0;

    }
    .nav{
        height:var(--header-height);
    }
    .navMenu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba( 0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0 ;
        transition: .3s;

    }
    /* SHOW MEDU */
    .showMenu{
        bottom: 0;
    }
    .navList {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-gap:2rem;
        gap:2rem;
    }
    .navIcon{
        font-size: 1.2rem;
    }
    .navClose{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }
    .navClose:hover{
        color: var(--title-color-dark);
    }
    .navToggle{
        font-size: 1.1rem;
        cursor: pointer;
    }
    .navIcon,.navClose    ,.navToggle{
        display: block;
    }
}
@media screen and (max-width:350px) {
    .navMenu{
        padding: 2rem 0.25rem 4rem ;
    }
    .navList{
        grid-column-gap: 0;
        -webkit-column-gap: 0;
                column-gap: 0;
    }
}
